.resume{
  .info-section{
    width: 20%;
  }
  .content-area{
    background: $color3;
    margin-left: 20%;
    width: 80%;
    min-width: 300px;
    background: white;
    height: 100%;
    overflow: hidden;

    .category{
      margin-bottom: 56px;
      h1{
        font-weight: 700;
        font-size: 2rem;
        text-transform: uppercase;
        margin-bottom: 56px;
      }

      .xp-section{
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-column-gap: 56px;
        grid-row-gap: 30px;
        max-width: 1000px;

        .xp-box{
          padding: 30px;
          transition: all .5s ease-in-out;
          cursor: pointer;
          &:hover{
            -webkit-box-shadow: 0px 20px 60px 17px rgba(41,127,255,0.12);
      -moz-box-shadow: 0px 20px 60px 17px rgba(41,127,255,0.12);
      box-shadow: 0px 20px 60px 17px rgba(41,127,255,0.12);
          }

          h5{
            font-weight: 500;
            font-size: 1.3rem;
            margin-bottom: 1.4rem;
          }
          p{
            font-weight: 300;
            font-size: 1rem;
            margin-bottom: 1rem;

          }
        }
      }
    }
  }
}



// phone
@media (min-width: 0px)and (max-width: 767px) {
  body,
  html{
    height: auto;
  }
 .resume{
   .menu-icon{
     top: -3px;
     right: 20px;
     color: white;

     .fa{
       color: white;

     }
   }
   .content-area{
     margin-left: 0;
     width: 100%;

     height: auto !important;

     .scroll-section{
       padding: 50px 20px;
       width:100%;
       height: auto;
       overflow: auto;
     }

     &:after{
       content: "";
       display: table;
       clear: both;
     }
   }
   .info-section{
     // display: none;

     padding: 10px 30px;
     // left: 0;
     // bottom: 0;
     // top: 0;
     position: static;
     // width: 35%;
     .logo{
       height: 50px;
       width: 70px;
       font-size: 1rem;
     }
     .info-box{
       display: none;
     }
   }

   .category{
     width: 100%;
     .xp-section{
       grid-template-columns: 1fr;
     }
   }
 }

}

// tablet
@media (min-width: 768px) and (max-width: 991px){

 .resume{
   .info-section{
     width: 30%;
     padding: 56px 30px;
     .icon-section{

       a{
         font-size: 1.4rem;
       }
     }
   }
   .content-area{
     height: auto;
     width: 70%;
     margin-left: 30%;

     &:after{
       content: "";
       display: table;
       clear: both;
     }
     .category{
       width: 100%;
       .xp-section{
         grid-template-columns: 1fr;
       }
     }
   }
 }
}
