.logo{
  text-transform: uppercase;
  border: 5px solid white;
  height: 130px;
  width: 150px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  color: white;
  line-height: 0;

}
.info-section{
  padding: 54px;
  width: 35%;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  .info-box{

    position: absolute;
    bottom: 60px;
    h4{
      font-size: 1.2rem;
      color: $color3;
      margin: 0.7rem 0;
    }
    p{
      font-size: 0.9rem;
      font-weight: 300;
      color: $color3;
      margin: 0.2rem 0 1.5rem;
    }
  }
  .icon-section{
    a{
      color: white;
      text-decoration: none;
      font-size: 2rem;
      padding: 0 10px;
      &:hover{
        color: #d930c0;
      }
      &:first-child{
        padding: 0 10px 0 0;
      }
    }
  }
}
