.home{

  .info-section{
    padding: 54px;
    width: 35%;

  }
  .content-area{
    background: $color3;
    margin-left: 35%;
    width: 65%;
    background: white;
    height: 100%;
    // overflow: auto;
    // padding: 0 0 0 25%;
    .img-container{
      display: inline-block;
      width: 30%;
      position: relative;
      height: 100%;

      .user-img{
        position: absolute;
        top: 56px;
        left: -200px;
        width: 450px;
        height: calc(100% - 112px);
        background: url("../img/brooks-back-turned.png") no-repeat center center ;
      -webkit-background-size: cover;
      -moz-background-size: cover;
      -o-background-size: cover;
      background-size: cover;
      -webkit-box-shadow: 0px 0px 80px 17px rgba(41,127,255,0.24);
-moz-box-shadow: 0px 0px 80px 17px rgba(41,127,255,0.24);
box-shadow: 0px 0px 80px 17px rgba(41,127,255,0.24);
      }
    }



    .profile-section{
      vertical-align: top;
      display: inline-block;
      padding: 20% 0 0 0;
      max-width: 500px;


      .profile{
        h1{
          font-size: 2.3rem;
          font-weight: 500;
          line-height: 1.2;
          margin: 0 0 1rem 0;

          span{
            color: $color1;
          }

        }
        h5{
          font-size: 1.1rem;
          margin: 0 0 2rem;
        }
        p{
          color: $color5;
          font-weight: 300;
          line-height: 1.4;
          margin: 0 0 2rem 0;
        }
      }
    }
  }
}


// phone
@media (max-width: 767px) {
  .home{
    .menu-icon{
      top: -3px;
      right: 20px;
      color: white;

      .fa{
        color: white;

      }
    }
    .content-area{
      height: auto;
      &:after{
        content: "";
        display: table;
        clear: both;
      }
    }
    .info-section{
      width: 30%;
      padding: 56px 30px;


      .icon-section{
        a{
          font-size: 1.4rem;
        }
      }

      .logo{
        height: 50px;
        width: 70px;
        font-size: 1rem;
      }
      .info-box{
        display: none;
      }
    }
    .content-area{
      margin-left: 0;
      width: 100%;
      .img-container{
        padding: 56px 20px 0;

        .user-img{
          position: static;
          width: 100%;
        }
      }
      .profile-section{
        width: 70%;
        float: right;
        padding:20% 20px 0;


      }
    }
  }
}
// tablet
@media (min-width: 768px) and (max-width: 991px){
  .home{
    .info-section{
      width: 30%;
      padding: 56px 30px;
      .icon-section{

        a{
          font-size: 1.4rem;
        }
      }
    }
    .content-area{
      margin-left: 30%;
      width: 70%;

      .img-container{
        padding: 56px 20px 0;
        .user-img{
          position: static;
          width: 100%;
        }
      }
      .profile-section{
        width: 70%;
        float: right;
        padding:20% 20px 0;
      }
    }
  }
}
// laptop
@media (min-width: 992px) and (max-width: 1199px) {
  .home{
    .content-area{
      .img-container{
        padding: 56px 20px 0;
        float: left;
        .user-img{
          position: static;
          width: 100%;
        }
      }

      .profile-section{
        width: 70%;
        float: left;
        padding:20% 20px 0;
        max-width: 800px;


      }
    }
  }
}
